
import { defineComponent, getCurrentInstance, ref } from "vue";
import { RadioGroup, Radio, Cell, CellGroup, Button, Row, Col } from "vant";
export default defineComponent({
  components: {
    VanRadio: Radio,
    VanRadioGroup: RadioGroup,
    VanCell: Cell,
    VanCellGroup: CellGroup,
    VanButton: Button,
    VanRow: Row,
    VanCol: Col,
  },
  setup() {
    let config = JSON.parse(localStorage.getItem("config") || "null");
    const LETTER = ["A", "B", "C", "D", "E", "F"];
    let isError = ref(false);
    const subjectList = (getCurrentInstance() as any).appContext.config
      .globalProperties.$subject["single"];
    console.log(subjectList[0]);
    const indexNum = ref(0);
    const radio = ref("");

    if (config && config["singleNum"]) {
      indexNum.value = config["singleNum"] - 1;
    }
    const upper = () => {
      if (!calculation() && !isError.value) {
        isError.value = true;
        return;
      }
      indexNum.value--;
      radio.value = "";
      isError.value = false;
    };
    const lower = () => {
      if (!calculation() && !isError.value) {
        isError.value = true;
        return;
      }
      indexNum.value++;
      radio.value = "";
      isError.value = false;
    };
    let calculation = () => {
      return radio.value === subjectList[indexNum.value].answer;
    };
    const shuffle = (arr: any) => {
      let i = arr.length;
      while (i) {
        let j = Math.floor(Math.random() * i--);
        [arr[j], arr[i]] = [arr[i], arr[j]];
      }
    };
    // //
    const handle = () => {
      subjectList.forEach((element: any) => {
        if (config && config["isUpset"]) {
          shuffle(element.option);
        }

        element.option.map((item: any, index: number) => {
          item.letter = LETTER[index];
          return item;
        });
      });
      subjectList.forEach((element: any) => {
        let answer = element.answer;
        let optionArr = element.option;

        element.answerLetter = optionArr.find(
          (item2: any) => item2.coordinate == answer
        )["letter"];
      });
    };
    return {
      isError,
      subjectList,
      indexNum,
      radio,
      handle,
      upper,
      lower,
    };
  },
  created() {
    this.handle();
  },
});
