
import { defineComponent, getCurrentInstance, ref } from "vue";
import {
  Checkbox,
  CheckboxGroup,
  Cell,
  CellGroup,
  Button,
  Row,
  Col,
} from "vant";
export default defineComponent({
  components: {
    VanCheckbox: Checkbox,
    VanCheckboxGroup: CheckboxGroup,
    VanCell: Cell,
    VanCellGroup: CellGroup,
    VanButton: Button,
    VanRow: Row,
    VanCol: Col,
  },
  setup() {
    let config = JSON.parse(localStorage.getItem("config") || "null");
    const LETTER = ["A", "B", "C", "D"];
    let isError = ref(false);

    const subjectList = (getCurrentInstance() as any).appContext.config
      .globalProperties.$subject["many"];
    const indexNum = ref(0);

    const checked = ref([]);
    const checkboxRefs = ref([]);
    const toggle = (index: any) => {
      (checkboxRefs.value[index] as any).toggle();
    };

    if (config && config["manyNum"]) {
      
      indexNum.value = config["manyNum"] - 1;
    }

    const upper = () => {
      if (!calculation() && !isError.value) {
        isError.value = true;
        return;
      }
      indexNum.value--;
      checked.value = [];
      isError.value = false;
    };
    const lower = () => {
      if (!calculation() && !isError.value) {
        isError.value = true;
        return;
      }

      indexNum.value++;
      checked.value = [];
      isError.value = false;
    };

    let calculation = () => {
      console.log(checked.value);
      const checkedArr = checked.value.map((item, index) => {
        return item;
      });
      let checkedAnswer = checkedArr.sort().join("");

      return checkedAnswer === subjectList[indexNum.value].answer;
    };
    const shuffle = (arr: any) => {
      let i = arr.length;
      while (i) {
        let j = Math.floor(Math.random() * i--);
        [arr[j], arr[i]] = [arr[i], arr[j]];
      }
    };

    //
    const handle = () => {
      subjectList.forEach((element: any) => {
        if (config && config["isUpset"]) {
          shuffle(element.option);
        }

        element.option.map((item: any, index: number) => {
          item.letter = LETTER[index];
          return item;
        });
      });
      subjectList.forEach((element: any) => {
        let answerArr = element.answer.split("");
        let optionArr = element.option;

        let answerLetterArr = answerArr.map((item: any) => {
          return optionArr.find((item2: any) => item2.coordinate == item)[
            "letter"
          ];
        });
        element.answerLetter = answerLetterArr.sort().join("");
      });
    };
    return {
      isError,
      subjectList,
      indexNum,
      checked,
      checkboxRefs,
      handle,
      toggle,
      upper,
      lower,
    };
  },
  created() {
    this.handle();
  },
  methods: {
    //
    // shuffle(arr) {
    //   let i = arr.length;
    //   while (i) {
    //     let j = Math.floor(Math.random() * i--);
    //     [arr[j], arr[i]] = [arr[i], arr[j]];
    //   }
    //   console.log(arr, 1);
    // },
  },
});
