
import { defineComponent, getCurrentInstance } from "vue";
import { reactive } from "vue";
import { Form, Field, CellGroup, Button, Switch } from "vant";
import { Toast, Divider } from "vant";
export default defineComponent({
  components: {
    VanForm: Form,
    VanField: Field,
    VanCellGroup: CellGroup,
    VanButton: Button,
    VanSwitch: Switch,
    VanDivider: Divider,
  },
  setup() {
    //
    const subject = (getCurrentInstance() as any).appContext.config
      .globalProperties.$subject;
    //
    let formData = reactive({
      manyNum: 1,
      judgeNum: 1,
      singleNum: 1,
      isUpset: false,
    });

    //
    let json = JSON.parse(localStorage.getItem("config") || "null");
    if (json) {
      formData.manyNum = json.manyNum;
      formData.judgeNum = json.judgeNum;
      formData.singleNum = json.singleNum;
      formData.isUpset = json.isUpset;
    }
    //
    const saveBtn = () => {
      let manyLength = subject["many"].length;
      let judgeLength = subject["judge"].length;
      let singleLength = subject["single"].length;
      if (formData.manyNum < 1 || formData.manyNum > manyLength) {
        Toast(`1-${manyLength}`);
        return;
      }
      if (formData.judgeNum < 1 || formData.judgeNum > judgeLength) {
        Toast(`1-${judgeLength}`);
        return;
      }
      if (formData.singleNum < 1 || formData.singleNum > singleLength) {
        Toast(`1-${singleLength}`);
        return;
      }

      localStorage.setItem("config", JSON.stringify(formData));
      Toast.success(``);
    };

    return {
      formData,
      saveBtn,
    };
  },
});
